
.Icon-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-container-check {
  position: absolute;
  left: 12px;
}
.Icon-container-check:hover {
  color: green;
  border: 2px solid #AAD9BB;
  border-radius: 50%;
  box-sizing: border-box;
}
.Icon-container-check--active {
  color: #4caf50;
}

.Icon-container-delete {
  position: absolute;
  right: 12px;
}
.Icon-container-delete:hover {
  color: red;
  border: 2px solid #AAD9BB;
  border-radius: 50%;
  box-sizing: border-box;
}

.Icon-svg {
  width: 24px;
  height: 24px;
}

.Icon-container-check:hover .Icon-svg {
  fill: green;
}
.Icon-container-delete:hover .Icon-svg {
  fill: red;
}
